import React from 'react';
import Header from './components/Header';
import Sections from './components/Sections';
import Footer from './components/Footer';

const Layout = () => {
    const headerData = {
        imgLogoUrl: 'https://i.ibb.co/mNBzXkz/logo.png',
        menuLinks:[
            { titulo:'Home', link:'index.html' },
            { titulo:'Sprint Zero', link:'sprintzero.html' },
            { titulo:'Innovation Squad', link:'innovationsquad.html' },
            { titulo:'Blog', link:'blog.html' }
        ]
    };

    return(
        <div>
            <Header headerData={headerData} />
            <Sections />
            <Footer />
        </div>
)};
export default Layout;