import React from 'react';

const Solucoes = (props) => {

  const solucoesData = props.solucoesData;
  let lstSolucoes = [];
  if (solucoesData.lstSolucoes && solucoesData.lstSolucoes.length) {
    lstSolucoes = solucoesData.lstSolucoes.map((soluc, i) => {
      return (
        <a href={soluc.link} key={i}>
          <div className="bg-sprint" data-aos="fade-left" style={{ backgroundImage: 'url(' + soluc.imgUrlFundo + ') no-repeat' }}>
            <div className="row">
              <div className="col-md-5 text-center">
                <div className="img-solucoes">
                  <img src={soluc.imgUrl} alt="Sprint zero" />
                </div>
              </div>
              <div className="col-md-7">
                <h3>
                  {soluc.descricao}
                </h3>
                <div className="link-area">
                  <span className="a-style hvr-wobble-vertical">Saiba mais</span>
                </div>
              </div>
            </div>
          </div>
        </a>);
    });
  }

  let lstDepoimentos = [];
  if (solucoesData.lstDepoimentosClientes && solucoesData.lstDepoimentosClientes.length) {
    lstDepoimentos = solucoesData.lstDepoimentosClientes.map((dep, i) => {
      return (
        <div className={'carousel-item' + (!i ? ' active' : '')} key={i}>
          <div className="row">
            <div className="col-md-6">
              <img src={dep.imgUrl} alt="Concore" />
            </div>
            <div className="col-md-6">
              <h4>{dep.titulo}</h4>
              <h3>{dep.subTitulo}</h3>
              <p>{dep.descricao}</p>
            </div>
          </div>
        </div>);
    });
  }

  return (
    <section className="solucoes">
      <div className="container">
        <div className="row">
          <div className="col-md-12 text-center" data-aos="fade-up">
            <h2>Soluções Concore</h2>
            <img className="divider2" src="https://i.ibb.co/wCJj8hV/divider-gradient.png" alt="Gradient divider" />
          </div>
          <div className="col-md-12">
            {lstSolucoes}
          </div>
        </div>
      </div>
      <div className="slider-concore" data-aos="fade-up">
        <div className="container">
          <div className="row">
            <div className="col-md-12 text-center">
              <h2>Confira o impacto das soluções Concore para seus clientes.</h2>
              <img className="divider2" src="https://i.ibb.co/wCJj8hV/divider-gradient.png" alt="Gradient divider" />
            </div>
            <div className="col-md-12">
              <div id="carouselConcore" className="carousel slide" data-ride="carousel" data-interval="5000">
                <div className="carousel-inner">
                  {lstDepoimentos}
                </div>
                <a className="carousel-control-prev car-control" href="#carouselConcore" role="button" data-slide="prev">
                  <i className="fas fa-chevron-left"></i>
                </a>
                <a className="carousel-control-next car-control" href="#carouselConcore" role="button" data-slide="next">
                  <i className="fas fa-chevron-right"></i>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
};

export default Solucoes;