import React from  'react';

const Header = (props) =>  {

    const logoUrl = props.headerData.imgLogoUrl;
    const menuLinks = props.headerData.menuLinks;

    let logoLink='';
    let links = [];
    if (menuLinks && menuLinks.length){
        links = menuLinks.map((item, i) => (<li  key={i}><a className="link" href={item.link} data-link="true">{item.titulo}</a></li>));    
        logoLink=menuLinks[0].link;
    }

    


    return (
        <header>
            <div className="container">
                <div className="row">
                    <nav className="navbar navbar-expand-md navbar-light nav-header col-md-12">
                        <div className="menu-holder">
                            <a href={logoLink}>
                                <img className="logo" src={logoUrl} alt="Concore" />
                            </a>
                        </div>
                        <button className="navbar-toggler collapsed" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-icon"><img src="https://i.ibb.co/fdn2xrR/hamburguer.png" alt="Menu" /></span>
                        </button>
                        <div className="navbar-collapse collapse" id="navbarSupportedContent">
                            <ul className="navbar-nav mx-auto">
                                {links}
                            </ul>
                        </div>
                    </nav>
                </div>
            </div>
        </header>
    )
};

export default Header;