import React, { Component } from "react";
class Footer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            nome: '',
            email: ''
        };

        this.nomeChange = this.nomeChange.bind(this);
        this.emailChange = this.emailChange.bind(this);
        this.submitHandler = this.submitHandler.bind(this);
    };

    nomeChange(e) {
        this.setState({ nome: e.target.value });
    }

    emailChange(e) {
        this.setState({ email: e.target.value });
    }

    submitHandler(e) {
        e.preventDefault();
        console.log(this.state.nome, this.state.email);

        fetch('https://flowcore.concore.io/rh/test/react1', {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                name: this.state.nome,
                email: this.state.email,
            }),
        }).then(res => res.json())
        .then(
            response => alert('E-mail enviado com sucesso: ' + JSON.stringify(response))
        )
        .catch(
            error => console.error('Erro ao tentar enviar o e-mail: ', error)
        );


    }

    render() {
        return (
            <footer>
                <div className="container">
                    <div className="row">
                        <div className="col-md-3 text-center">
                            <img src="https://i.ibb.co/TwmbBYF/logo-footer.png" alt="Logo Concore" />
                            <ul>
                                <li><a href="https://www.facebook.com/concore.io/" target="_blank" rel="noopener noreferrer"><img src="https://i.ibb.co/jr84Tb7/facebook.png" alt="Facebook Concore" /></a></li>
                                <li><a href="https://www.linkedin.com/company/concore.io/" target="_blank" rel="noopener noreferrer"><img src="https://i.ibb.co/FbXYT8S/linkedin.png" alt="Linkedin Concore" /></a></li>
                                <li><a href="https://www.instagram.com/concore.oficial/" target="_blank" rel="noopener noreferrer"><img src="https://i.ibb.co/hdV4yHx/instagram.png" alt="Instagram Concore" /></a></li>
                            </ul>
                        </div>
                        <div className="col-md-4 offset-md-1">
                            <h5>Assine nossa newsletter</h5>

                            <form className="form-style" onSubmit={this.submitHandler}>
                                <div className="form-group">
                                    <input required type="text"
                                        placeholder="Nome:"
                                        className="form-control"
                                        value={this.state.nome}
                                        onChange={this.nomeChange} />
                                </div>
                                <div className="form-group">
                                    <input required type="text"
                                        placeholder="E-mail:"
                                        className="form-control"
                                        value={this.state.email}
                                        onChange={this.emailChange} />
                                </div>
                                <div className="button-area">
                                    <button type="submit">Enviar</button>
                                </div>
                            </form>
                        </div>
                        <div className="col-md-4 text-center-mobile">
                            <h5>Contatos</h5>
                            <p><a href="mailto:">contato@concore.io</a></p>
                            <p>Alameda Franca, 243 Bela Vista SP</p>
                        </div>
                    </div>
                </div>
                <hr />
                <div className="container">
                    <div className="row">
                        <div className="col-md-12 text-center">
                            <p className="creditos">Copyright © 2019 Concore. Todos os direitos reservados. </p>
                        </div>
                    </div>
                </div>
            </footer>
        );
    }
}

