import React from  'react';

const Sobre = (props) => {
    const sobreData = props.sobreData;
    let lstDiferenciais = [];

    if (sobreData.lstDiferenciais && sobreData.lstDiferenciais.length){
        lstDiferenciais = sobreData.lstDiferenciais.map((dif, i) => {
            return (
                <div className="col-md-4 text-center" data-aos="fade-up" key={i}>
                    <div className="box-df">
                        <div className="div-img">
                        <img src={dif.imgUrl} alt="4x mais rápido"/>
                        </div>
                        <hr className="hr-g" />
                        <p dangerouslySetInnerHTML={{ __html: dif.descricao || '' }}></p>
                    </div>
                </div>
            );
        });    
    }

    return (
        <section className="sobre">
            <div className="container">
                <div className="row" data-aos="fade-up">
                    <div className="col-md-5">
                        <img className="img-width" src={sobreData.imgUrl} alt="Concore"/>
                    </div>
                    <div className="col-md-7">
                        <h2>Grande parte dos projetos de software falham em custo, prazo<br/>ou valor entregue.</h2>
                        <img className="divider1" src="https://i.ibb.co/wCJj8hV/divider-gradient.png" alt="Gradient divider" />
                        
                        <div dangerouslySetInnerHTML={{ __html: sobreData.descricao || '' }} />
                    </div>

                    <div className="col-md-12 text-center" data-aos="fade-down">
                        <h2 className="title-d">Os diferenciais Concore para fazer seu negócio crescer.</h2>
                        <img className="divider2" src="https://i.ibb.co/wCJj8hV/divider-gradient.png" alt="Gradient divider"/>
                    </div>
                    {lstDiferenciais}
                </div>
            </div>
        </section>
    )
};

export default Sobre;