import React from  'react';

const Banner = (props) => {

    const bannerData = props.bannerData;

    return (
        <section className="banner" style={{ backgroundImage: 'url(' + bannerData.urlImgFundo + ')'}}>
            <div className="container">
                <div className="row">
                    <div className="col-md-12">
                        <h1 dangerouslySetInnerHTML={{ __html: bannerData.titulo || '' }}>
                        </h1>
                        <p dangerouslySetInnerHTML={{ __html: bannerData.fraseDescricao || '' }}>
                        </p>
                        <div className="link-scroll">
                            <a href="javascript;" className="scrolldown hvr-wobble-vertical">
                                <i className="fas fa-chevron-down"></i>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
};

export default Banner;